import React from 'react';
import { Button, Result } from 'antd';
import { Link } from 'react-router-dom';

export default function NotFound() {
  return (
    <Result
      title="404"
      status="404"
      subTitle="Not Found"
      extra={<Button type="primary"><Link to="/temprate/">Back Home</Link></Button>}
      />
  );
}