import React from "react";
import { connect } from 'react-redux';
import { PageHeader, Button, Typography, Badge, Row, Col, Dropdown, Menu } from "antd";
import { Link } from "react-router-dom";
import { DownOutlined, UserOutlined, OrderedListOutlined } from '@ant-design/icons';
import './AppHeader.css';

const { Text } = Typography;

function AppHeader(props) {
  const { user } = props;
  const { currentUser } = user;

  // console.log("AppHeader ", currentUser) -> OK
  const menu = (
    <Menu>
      <Menu.Item>
        <Link to="/temprate/logout">Logout</Link>
      </Menu.Item>
    </Menu>
  );

  const userInfo = (
    <Dropdown overlay={menu}>
      <Text type="secondary" mark code>
        Helo {currentUser.username} <DownOutlined />
      </Text>
    </Dropdown>
  )

  const loginBtn = (currentUser && currentUser.isLogged) ? userInfo : (<Button key="1" type="primary">
  <Link to="/temprate/login"><UserOutlined /> Login</Link>
  </Button>);
  return (
    <div className="AppHeader">
          <PageHeader
            className="site-page-header"
            onBack={() => window.history.back()}
            title={<Link to="/temprate">Test Online</Link>}
            subTitle="phiên bản dùng thử"
          >
            <Row>
              <Col>
                <Button.Group className="btn-group">
                <Button key="3"><Link to="/temprate/examlist"><OrderedListOutlined />List Of Exam</Link></Button>
                {/* <Button key="2"><Link to="/signup"><UserAddOutlined />Signup</Link></Button> */}
                {loginBtn}
                </Button.Group>
              </Col>
              <Col>
              </Col>
            </Row>      
          </PageHeader>          
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    user: state.user
  }
}

export default connect(mapStateToProps)(AppHeader);
